body {
  color: #faebd7;
  background-color: #1a1a1a;
  min-height: 100vh;
  margin: 0;
  font-family: Arial, sans-serif;
}

.container {
  flex-direction: column;
  align-items: center;
  display: flex;
}

.logo {
  color: #ff6bff;
  text-align: center;
  text-shadow: 0 0 15px #ff6bffb3;
  margin-bottom: 2rem;
  font-size: 3rem;
  font-weight: bold;
}

.back-to-explore-btn {
  margin-top: 30px;
  padding: 5px;
  font-size: 1rem;
}

.artist-grid {
  grid-template-columns: repeat(3, 1fr);
  gap: 30px;
  margin-top: 30px;
  display: grid;
}

.artist-card {
  cursor: pointer;
  text-align: center;
  border: 1px solid #ff6bff;
  border-radius: 4px;
  padding-top: 20px;
}

.artist-image {
  border-radius: 8px;
  width: 85%;
  height: auto;
}

.artist-card h3 {
  margin-top: 10px;
  font-size: 1.5em;
  font-weight: bold;
}

.search-bar-container {
  background-color: #1a1a1a;
  border: 1px solid #333;
  border-radius: 8px;
  align-items: center;
  gap: 12px;
  width: 50%;
  padding: 12px 20px;
  display: flex;
}

.search-icon {
  color: #e0e0e0;
  font-size: 20px;
}

.search-input {
  color: #fff;
  background: none;
  border: none;
  outline: none;
  width: 100%;
  font-size: 16px;
}

.search-input::placeholder {
  color: #b0b0b0;
}

.search-results {
  gap: 20px;
  width: 50%;
  max-width: 1400px;
  margin: 0 auto;
  padding: 20px;
}

.track-container {
  background-color: #2a2a2a;
  border-radius: 8px;
  justify-content: space-between;
  margin-bottom: 15px;
  padding: 15px;
  transition: all .3s;
  display: flex;
}

.track-container:hover {
  background-color: #3a3a3a;
}

.track-info {
  flex-direction: column;
  gap: 10px;
  display: flex;
}

.track-text {
  gap: 5px;
  margin-bottom: 10px;
}

.track-actions {
  border-left: 1px solid #444;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  gap: 15px;
  margin-left: 15px;
  padding-left: 15px;
  display: flex;
}

.action-btn {
  color: #b0b0b0;
  cursor: pointer;
  background: none;
  border: none;
  padding: 5px;
  font-size: 1.2rem;
  transition: all .2s;
}

.action-btn:hover {
  color: #ff6bff;
  transform: scale(1.1);
}

.like-btn {
  align-items: center;
  gap: 5px;
  display: flex;
}

.comment-container {
  flex-direction: column;
  height: 300px;
  display: flex;
}

.comment-input-container {
  flex-direction: column;
  gap: 8px;
  margin-top: 10px;
  display: flex;
}

.comment-input {
  color: #fff;
  background: #1a1a1a;
  border: 1px solid #444;
  border-radius: 4px;
  width: 250px;
  padding: 8px 12px;
  font-size: .9rem;
}

.comment-input:focus {
  border-color: #ff6bff;
  outline: none;
}

.submit-comment-btn {
  color: #fff;
  cursor: pointer;
  background-color: #ff6bff;
  border: none;
  border-radius: 4px;
  padding: 6px 12px;
  font-size: .8rem;
  transition: background-color .2s;
}

.submit-comment-btn:hover {
  background-color: #ff8fab;
}

.load-comments-btn {
  color: #1a1a1a;
  background-color: #fff;
  border-radius: 4px;
  margin-top: 10px;
  padding: 6px 12px;
  font-size: .8rem;
}

.comments {
  background: #1a1a1a;
  border: 1px solid #444;
  font-size: .9rem;
}

.single-comment {
  text-align: left;
  color: #ddd;
  border-bottom: 1px solid #333;
  padding: 6px 10px;
}

.load-comment {
  text-align: left;
  color: #bbb;
  padding: 10px;
  font-style: italic;
}

@media (width <= 768px) {
  .track-container {
    flex-direction: column;
  }

  .track-actions {
    border-top: 1px solid #444;
    border-left: none;
    flex-direction: row;
    justify-content: center;
    margin-top: 15px;
    margin-left: 0;
    padding-top: 15px;
    padding-left: 0;
  }
}

.login-modal-overlay {
  z-index: 1000;
  background-color: #000000b3;
  justify-content: center;
  align-items: center;
  display: flex;
  position: fixed;
  inset: 0;
}

.login-modal {
  background-color: #2a2a2a;
  border-radius: 8px;
  width: 90%;
  max-width: 400px;
  padding: 25px;
  box-shadow: 0 4px 20px #0000004d;
}

.login-modal h3 {
  color: #fff;
  margin-bottom: 15px;
}

.login-modal p {
  color: #b0b0b0;
  margin-bottom: 20px;
}

.login-input {
  color: #fff;
  background-color: #1a1a1a;
  border: 1px solid #444;
  border-radius: 4px;
  width: 100%;
  margin-bottom: 20px;
  padding: 12px;
}

.login-input:focus {
  border-color: #ff6bff;
  outline: none;
}

.login-buttons {
  justify-content: flex-end;
  gap: 10px;
  display: flex;
}

.login-submit-btn {
  color: #fff;
  cursor: pointer;
  background-color: #ff6bff;
  border: none;
  border-radius: 4px;
  padding: 8px 16px;
}

.login-submit-btn:hover {
  background-color: #ff8fab;
}

.login-cancel-btn {
  color: #b0b0b0;
  cursor: pointer;
  background-color: #0000;
  border: 1px solid #444;
  border-radius: 4px;
  padding: 8px 16px;
}

.login-cancel-btn:hover {
  background-color: #3a3a3a;
}
